@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Montserrat', sans-serif;
}

.container{
  width: 900px;
  margin: 0 auto;
}

.header{
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  padding: 10px 0;
  margin-bottom: 40px;
}

.header .container{
  display: flex;
  justify-content: space-between;
}

.logo{
  height: 50px;
}

.menu__content{
  list-style-type: none;
  display: flex;
}

.menu__item{
  margin: 0 15px;
}

.menu__link{
  color: #000;
  text-decoration: none;
}

.menu__link:hover{
  text-decoration: underline;
}

h1{
  text-align: center;
  font-weight: 900;
  margin-bottom: 40px;
}

.item{
  box-shadow: 0 2px 15px rgba(0,0,0, 0.2);
  margin-bottom: 20px;
}

.item__title{
  position: relative;
  padding: 15px 40px 15px 15px;
  font-weight: bold;
  cursor: pointer;
  font-size: 20px;
}

.item__toggle{
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 15px;
}

.item__content{
  padding: 15px;
}

.github-link{
  font-weight: bold;
}

.github-link[href^="http"]{
  position: relative;
  color: red;
}

.github-link[href^="http"]::before{
  content: '';
  width: 20px;
  height: 20px;
  background: url(/react-router/static/media/github-logo.76d344f0.svg) no-repeat center;
  background-size: contain;
  position: absolute;
  right: -30px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: none;
}

.github-link[href^="http"]:hover::before{
  display: block;
}

.center-flex{
  display: flex;
  justify-content: center;
}

.center{
  text-align: center;
}

.home__img{
  width: 100%;
}
.mentors{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.mentor{
    width: 33%;
    padding: 0 20px;
    flex: 1 1;
    text-align: center;
    margin-bottom: 40px;
}

.mentor__photo{
    overflow: hidden;
    border-radius: 50%;
    width: 300px;
    margin: 0 auto;
}

.mentors__links{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 40px;
}

.mentors__link{
	font-weight: bold;
	text-transform: uppercase;
	font-size: 12px;
	color: #000;
	z-index: 2;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	position: relative;
	padding: 8px 0 5px;
    display: inline-block;
    margin: 0 10px;
    text-decoration: none;
}

.mentors__link:before, .mentors__link:after{
    content: '';
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #000;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
}

.mentors__link:hover:before{
    top: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
}
.mentors__link:hover:after{
    -webkit-transform: scale(1);
            transform: scale(1);
}

.mentor__photo img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.mentor__name{
    font-weight: bold;
    margin: 15px 0 10px;
}

.mentor__email{
    color: #000;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.mentor__email:hover{
    color: red;
}
